import { useState } from 'react';
import { submitUserInfo } from '@/lib/geminiClient';

interface UserInfoFormProps {
  onSubmitSuccess: (submissionId: string) => void;
}

export default function UserInfoForm({ onSubmitSuccess }: UserInfoFormProps) {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setIsSubmitting(true);

    try {
      const result = await submitUserInfo(name, email);
      onSubmitSuccess(result.id);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to submit information');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="user-info-form-container">
      <div className="user-info-form-card">
        <h2>Welcome to Unit2Go Visualiser</h2>
        <p className="form-description">
          Before we begin, please provide your contact information. This helps us improve our service and keep you updated.
        </p>

        <form onSubmit={handleSubmit} className="user-info-form">
          <div className="form-group">
            <label htmlFor="name">Full Name *</label>
            <input
              type="text"
              id="name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              required
              placeholder="Enter your full name"
              disabled={isSubmitting}
            />
          </div>

          <div className="form-group">
            <label htmlFor="email">Email Address *</label>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
              placeholder="your.email@example.com"
              disabled={isSubmitting}
            />
          </div>

          {error && (
            <div className="error-message">{error}</div>
          )}

          <button
            type="submit"
            className="submit-button"
            disabled={isSubmitting}
          >
            {isSubmitting ? 'Submitting...' : 'Continue to Visualiser'}
          </button>
        </form>

        <p className="privacy-note">
          We respect your privacy. Your information will only be used to improve our service.
        </p>
      </div>
    </div>
  );
}
