/**
 * Client-side wrapper for Gemini API calls
 * Calls the Next.js API route instead of calling Gemini directly
 */

export async function generateImageViaAPI(params: {
  propertyImageBase64: string;
  tinyHomeImageBase64: string;
  prompt: string;
  aspectRatio: string;
  propertyImageMimeType?: string;
}): Promise<string> {
  const response = await fetch('/api/gemini', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      action: 'generateImage',
      ...params,
    }),
  });

  if (!response.ok) {
    const error = await response.json();
    throw new Error(error.error || 'Failed to generate image');
  }

  const data = await response.json();
  return data.imageUrl;
}

