export async function submitUserInfo(name: string, email: string, modelId?: string): Promise<{ id: string }> {
  const response = await fetch('/api/submissions', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({ name, email, modelId }),
  });

  if (!response.ok) {
    const error = await response.json();
    throw new Error(error.error || 'Failed to submit user info');
  }

  return await response.json();
}
